  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1"
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleOpen() {},
    handleClose() {}
  }
};
</script>
<style lang="less" scoped>
.el-container {
  height: 100%;
}
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #393e44;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
  height: calc(100% - 60px);
}
.footer-ul {
  margin: 0 auto;
  overflow: hidden;
  display: inline-block;
}
.footer-li {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  background: red;
  list-style: none;
}
.el-menu-vertical-demo {
  color: #fafafa;
}
</style>




